<template>
  <div id="app">
    <component :is="layout">
    </component>
  </div>
</template>

<script>

  const default_layout = "default";
  export default {
      computed: {
          layout() {
              return (this.$route.meta.layout || default_layout) + '-layout';
          }
      }
  }
</script>
<style lang="scss">
  @import 'assets/scss/style.scss';
</style>
